import { Button, Form, Input, message } from 'antd';
import React from 'react';
import styled from "styled-components";
import { Auth } from "../models";
import useStores from "../stores";
import { useNavigate } from "react-router-dom";

const StyBox = styled.div`
  margin: 30px auto;
  box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.2);
  padding: 30px;
  max-width: 800px;
  border-radius: 4px;
`;

const StyTitle = styled.h1`
  text-align: center;
`;

const validateUsername = () => ({
  validator(rule, value) {
    if(value.length < 4) return Promise.reject("最少4个字符");
    if(value.length > 8) return Promise.reject("最多8个字符");
    if(/\W/.test(value)) return Promise.reject("只能使用数字、字母或下划线");
    return Promise.resolve();
  }
});

const Login = () => {
  const {UserStore} = useStores();
  const navigate = useNavigate();

  const onFinish = (values) => {
    Auth.login(values.username, values.password)
      .then(() => {
        UserStore.resetUser();
        UserStore.pullUser(Auth.getCurrentUser());
        navigate("/");
      }).catch(() => {
        message.error("登录失败");
      });
  };

  const onFinishFailed = (errorInfo) => {
  };

  return (
    <StyBox>
      <StyTitle>登录</StyTitle>
      <Form
        name="basic"
        labelCol={{
          span: 6,
        }}
        wrapperCol={{
          span: 14,
        }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item
          label="用户名"
          name="username"
          rules={[
            {
              required: true,
              message: '请输入用户名',
            },
            validateUsername
          ]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="密码"
          name="password"
          rules={[
            {
              required: true,
              message: '请输入密码',
            },
            {
              min: 6,
              message: "最少6个字符"
            },
            {
              max: 10,
              message: "最多10个字符"
            }
          ]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          wrapperCol={{
            offset: 6,
            span: 18,
          }}
        >
          <Button type="primary" htmlType="submit">
            提交
          </Button>
        </Form.Item>
      </Form>
    </StyBox>
  );
};

export default Login;